import React from "react"
import { graphql } from "gatsby"
import { Markdown } from "grommet"
import { PropTypes } from "prop-types"
import useDarkMode from "use-dark-mode"

import Layout from "components/layout"
import SEO from "components/atoms/seo"
import Project from "components/atoms/project"
import Wrapper from "components/atoms/wrapper"
import Footer from "components/molecules/footer"

const ProjectPage = (query) => {
  const isDarkMode = useDarkMode(true).value
  const customBackground = isDarkMode ? "#111" : "light-1"

  const metadata = query.data.site.siteMetadata
  const { frontmatter, html } = query.data.markdownRemark

  return (
    <Layout>
      <SEO title={frontmatter.title} />
      <Project {...frontmatter}></Project>
      {html && (
        <Wrapper
          parentProps={{
            background: customBackground,
            border: {
              color: "dark-1",
              side: "horizontal",
            },
          }}
          childrenProps={{
            margin: { top: "large", bottom: "xlarge" },
            width: { max: "600px" },
          }}
        >
          <Markdown
            components={{
              p: {
                component: "p",
                props: { size: "medium" },
              },
            }}
          >
            {html}
          </Markdown>
        </Wrapper>
      )}
      <Footer {...metadata} />
    </Layout>
  )
}

export const pageQuery = graphql`
  query ($path: String!) {
    site {
      siteMetadata {
        about
        author
        categories
        intro
        location {
          city
          lat
          lng
        }
        social {
          # codepen
          dribbble
          github
          linkedin
          medium
          npm
          spotify
          # twitter
        }
        title
      }
    }
    markdownRemark(frontmatter: { path: { eq: $path } }) {
      html
      frontmatter {
        active
        case
        description
        iframe
        image
        logo
        path
        show
        tags
        title
        type
        website
        year
      }
    }
  }
`

ProjectPage.propTypes = {
  query: PropTypes.object,
}

export default ProjectPage
